
































import { ref, onMounted } from "../../../adapter-vue";
import { TUIStore, StoreName, TUIChatService, SendMessageParams, IConversationModel } from "@tencentcloud/chat-uikit-engine";
import { isH5, isWeChat, isApp } from "../../../utils/env";
import { TUIGlobal } from "../../../utils/universal-api/index";
import { isEnabledMessageReadReceiptGlobal } from "../utils/utils";
import Icon from "../../common/Icon.vue";
import audio from "../../../assets/icon/audio.svg";
const __sfc_main = {};
__sfc_main.props = {
  isAudioMainShow: {
    type: Boolean,
    default: false
  }
};
__sfc_main.setup = (__props, __ctx) => {
  const props = __props;
  const emits = __ctx.emit;
  const showAudioMain = ref<boolean>(false);
  const recorderManager = TUIGlobal?.getRecorderManager();
  const popupToggle = ref(false);
  const isRecording = ref(false);
  const canSend = ref();
  const textValue = ref("按住说话");
  const title = ref();
  const recordTime = ref();
  const recordTimer = ref(null);
  const currentConversation = ref<IConversationModel>();
  TUIStore.watch(StoreName.CONV, {
    currentConversation: (conversation: IConversationModel) => {
      currentConversation.value = conversation;
    }
  });
  const switchAudio = () => {
    showAudioMain.value = !showAudioMain.value;
    emits("switchAudio", showAudioMain.value);
  };
  onMounted(() => {
    // 加载声音录制管理器
    recorderManager.onStop((res: any) => {
      clearInterval(recordTimer.value);
      // 兼容 uniapp 打包app，duration 和 fileSize 需要用户自己补充
      // 文件大小 ＝ (音频码率) x 时间长度(单位:秒) / 8
      let msg = {
        duration: res.duration ? res.duration : recordTime.value * 1000,
        tempFilePath: res.tempFilePath,
        fileSize: res.fileSize ? res.fileSize : 48 * recordTime.value / 8 * 1024
      };
      TUIGlobal?.hideLoading();
      // 兼容 uniapp 语音消息没有duration
      if (canSend.value) {
        if (msg.duration < 1000) {
          TUIGlobal?.showToast({
            title: "录音时间太短",
            icon: "none"
          });
        } else {
          // res.tempFilePath 存储录音文件的临时路径
          const options = ({
            to: currentConversation?.value?.groupProfile?.groupID || currentConversation?.value?.userProfile?.userID,
            conversationType: currentConversation?.value?.type,
            payload: {
              file: msg
            },
            needReadReceipt: isEnabledMessageReadReceiptGlobal()
          } as SendMessageParams);
          TUIChatService?.sendAudioMessage(options);
        }
      }
      popupToggle.value = false;
      isRecording.value = false;
      canSend.value = true;
      title.value = " ";
      textValue.value = "按住说话";
    });
  });
  const handleLongPress = (e: any) => {
    popupToggle.value = true;
    recorderManager.start({
      duration: 60000,
      // 录音的时长，单位 ms，最大值 600000（10 分钟）
      sampleRate: 44100,
      // 采样率
      numberOfChannels: 1,
      // 录音通道数
      encodeBitRate: 192000,
      // 编码码率
      format: "mp3" // 音频格式，选择此格式创建的音频消息，可以在即时通信 IM 全平台（Android、iOS、微信小程序和Web）互通
    });
    title.value = "正在录音";
    isRecording.value = true;
    recordTime.value = 0;
    recordTimer.value = setInterval(() => {
      recordTime.value++;
    }, 1000);
  };

  // 录音时的手势上划移动距离对应文案变化
  const handleTouchMove = (e: any) => {
    if (isRecording.value) {
      if (e.currentTarget.offsetTop - e.changedTouches[e.changedTouches.length - 1].clientY > 100) {
        textValue.value = "抬起停止";
        title.value = "松开手指，取消发送";
        canSend.value = false;
      } else if (e.currentTarget.offsetTop - e.changedTouches[e.changedTouches.length - 1].clientY > 20) {
        textValue.value = "抬起停止";
        title.value = "上划可取消";
        canSend.value = true;
      } else {
        textValue.value = "抬起停止";
        title.value = "正在录音";
        canSend.value = true;
      }
    }
  };

  // 手指离开页面滑动
  const handleTouchEnd = () => {
    isRecording.value = false;
    popupToggle.value = false;
    textValue.value = "按住说话";
    TUIGlobal?.hideLoading();
    recorderManager.stop();
  };
  return {
    isH5,
    isWeChat,
    isApp,
    audio,
    props,
    popupToggle,
    textValue,
    title,
    switchAudio,
    handleLongPress,
    handleTouchMove,
    handleTouchEnd
  };
};
__sfc_main.components = Object.assign({
  Icon
}, __sfc_main.components);
export default __sfc_main;
